/* FONTS */

/* VARIABLE CSS */
:root {
  --header-height: 3.5rem;
  --header-margin-top: 5rem;

  /* Colors */
  --hue: 196;
  --first-color: hsl(var(--hue), 91%, 44%);
  --first-color-alt: hsl(var(--hue), 91%, 40%);
  --title-color: hsl(var(--hue), 64%, 40%);
  --title-color-light: hsl(var(--hue), 64%, 90%);
  --text-color: hsl(var(--hue), 64%, 35%);
  --text-color-light: hsl(var(--hue), 64%, 25%);

  /* Green gradient */
  --body-color: linear-gradient(90deg, hsl(75, 24%, 90%) 0%, hsl(19, 54%, 95%) 100%);
  --container-color: linear-gradient(136deg, hsl(75, 24%, 90%) 0%, hsl(19, 24%, 90%) 100%);
  

  /* Font and Typography */
  --body-font: 'Poppins', sans-serif;
  --biggest-font-size: 2rem;
  --h1-font-size: 1.5rem;
  --h2-font-size: 1.25rem;
  --h3-font-size: 1rem;
  --normal-font-size: .938rem;
  --small-font-size: .813rem;
  --smaller-font-size: .75rem;

  /* Font weight */
  --font-medium: 500;
  --font-semi-bold: 600;
  --font-black: 900;

  /* Margin Bottom */
  --mb-0-25: .25rem;
  --mb-0-5: .5rem;
  --mb-0-75: .75rem;
  --mb-1: 1rem;
  --mb-1-5: 1.5rem;
  --mb-2: 2rem;
  --mb-2-5: 2.5rem;

  /* z index */
  --z-tooltip: 10;
  --z-fixed: 100;
  --z-fixed: 100;
}

/* Responsive typography */
@media screen and (min-width: 992px) {
  :root {
    --biggest-font-size: 4rem;
    --h1-font-size: 2.25rem;
    --h2-font-size: 1.5rem;
    --h3-font-size: 1.25rem;
    --normal-font-size: 1rem;
    --small-font-size: .875rem;
    --smaller-font-size: .813rem;
  }
}

/* BASE */
* {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
}

html {
  scroll-behavior: smooth;
}

body {
  margin: 3.5rem 0 0 0;
  margin: var(--header-height) 0 0 0;
  font-family: 'Poppins', sans-serif;
  font-family: var(--body-font);
  font-size: .938rem;
  font-size: var(--normal-font-size);
  background: linear-gradient(90deg, hsl(75, 24%, 90%) 0%, hsl(19, 54%, 95%) 100%);
  background: var(--body-color);
  color: hsl(196, 64%, 35%);
  color: var(--text-color);
}

h1,h2,h3,h4 {
  color: hsl(196, 64%, 40%);
  color: var(--title-color);
  font-weight: 600;
  font-weight: var(--font-semi-bold);
}

ul {
  list-style: none;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
  height: auto;
}

/* .btn, */
input {
  border: none;
  outline: none;
}

.btn {
  cursor: pointer;
  font-family: 'Poppins', sans-serif;
  font-family: var(--body-font);
  font-size: .938rem;
  font-size: var(--normal-font-size);
  border: 1px solid hsl(196, 91%, 40%);
  border: 1px solid var(--first-color-alt);
  padding: 0 0.5rem;
}

/* REUSABLE CSS CLASSES */
.section {
  padding: 4.5rem 0 2rem;
}

.section__title {
  font-size: 1.25rem;
  font-size: var(--h2-font-size);
  margin-bottom: 2rem;
  margin-bottom: var(--mb-2);
  text-align: center;
}

/* LAYOUT */
.container {
  max-width: 968px;
  margin-left: 1.5rem;
  margin-left: var(--mb-1-5);
  margin-right: 1.5rem;
  margin-right: var(--mb-1-5);
}

.grid {
  display: grid;
}

.main {
  overflow: hidden;
}

/* NAV */
.nav {
  height: 3.5rem;
  height: var(--header-height);
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.nav__logo {
  display: flex;
  align-items: center;
  grid-column-gap: .5rem;
  -webkit-column-gap: .5rem;
          column-gap: .5rem;
  font-weight: 500;
  font-weight: var(--font-medium);
}

.nav__logo-img {
  width: 1.25rem;
}

.nav__link,
.nav__logo,
.nav__toggle,
.nav__close {
  color: hsl(196, 64%, 40%);
  color: var(--title-color);
}

.nav__toggle {
  font-size: 1.25rem;
  cursor: pointer;
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

@media screen and (max-width: 767px) {
  .nav__menu {
    position: fixed;
    width: 100%;
    background: linear-gradient(136deg, hsl(75, 24%, 90%) 0%, hsl(19, 24%, 90%) 100%);
    background: var(--container-color);
    top: -150%;
    left: 0;
    padding: 3.5rem 0;
    transition: .4s;
    z-index: 100;
    z-index: var(--z-fixed);
    border-radius: 0 0 1.5rem 1.5rem;
    box-shadow: 0 2px 2px rgb(0 0 0 / 40%);
  }
}

.nav__img {
  width: 100px;
  position: absolute;
  top: 0;
  left: 0;
}

.nav__close {
  font-size: 1.6rem;
  position: absolute;
  top: 0.5rem;
  right: 1.5rem;
  cursor: pointer;
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

.nav__list {
  display: flex;
  flex-direction: column;
  align-items: center;
  grid-row-gap: 1.5rem;
  row-gap: 1.5rem;
}

.nav__link {
  text-transform: uppercase;
  font-weight: 900;
  font-weight: var(--font-black);
  transition: .4s;
}

.nav__link:hover {
  color: hsl(196, 64%, 35%);
  color: var(--text-color);
}

.show-menu {
  top: 0;
}

/* Change background header */
.scroll-header {
  background: linear-gradient(136deg, hsl(75, 24%, 90%) 0%, hsl(19, 24%, 90%) 100%);
  background: var(--container-color);
}

/* Active link */
.active-link {
  position: relative;
}

.active-link::before {
  content: '';
  position: absolute;
  bottom: .4rem;
  left: -1.5rem;
  width: 8px;
  height: 8px;
  background-color: hsl(196, 64%, 40%);
  background-color: var(--title-color);
  border-radius: 50%;
}

/* HEADER */
.header {
  width: 100%;
  background: linear-gradient(90deg, hsl(75, 24%, 90%) 0%, hsl(19, 54%, 95%) 100%);
  background: var(--body-color);
  position: fixed;
  top: 0;
  left: 0;
  z-index: 100;
  z-index: var(--z-fixed);
}

