/* BUTTONS */
.btn {
  display: inline-block;
  /* background-color: var(--first-color); */
  color: hsl(196, 64%, 40%);
  color: var(--title-color);
  border-radius: .5rem;
  font-weight: 500;
  font-weight: var(--font-medium);
  transition: .3s;
  -webkit-user-select: none;
      -ms-user-select: none;
