/* Ads */
.ad {
  position: fixed;
  display: flex;
  justify-content: center;
  align-items: center;
}
.ad-970-90,
.ad-728-90,
.ad-300-50 {
  display: none;
  transition: bottom .4s;
}

.ad-970-90,
.ad-728-90 {
  bottom: -100px;
}
.ad-300-50 {
  bottom: -60px;
}
.ad-970-90.collapse,
.ad-728-90.collapse,
.ad-300-50.collapse {
  bottom: 0;
}
.ad-970-90 { left: calc((100% - 970px) / 2); }
.ad-728-90 { left: calc((100% - 728px) / 2); }
.ad-300-50 { left: calc((100% - 300px) / 2); }
@media screen and (min-width: 971px) { .ad-970-90 { display: block; } }
@media screen and (min-width: 768px) and (max-width: 970px) { .ad-728-90 { display: block; } }
@media screen and (max-width: 767px) { .ad-300-50 { display: block; } }

