/* FONTS */
@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@400;500;600;900&display=swap');

/* VARIABLE CSS */
:root {
  --header-height: 3.5rem;
  --header-margin-top: 5rem;

  /* Colors */
  --hue: 196;
  --first-color: hsl(var(--hue), 91%, 44%);
  --first-color-alt: hsl(var(--hue), 91%, 40%);
  --title-color: hsl(var(--hue), 64%, 40%);
  --title-color-light: hsl(var(--hue), 64%, 90%);
  --text-color: hsl(var(--hue), 64%, 35%);
  --text-color-light: hsl(var(--hue), 64%, 25%);

  /* Green gradient */
  --body-color: linear-gradient(90deg, hsl(75, 24%, 90%) 0%, hsl(19, 54%, 95%) 100%);
  --container-color: linear-gradient(136deg, hsl(75, 24%, 90%) 0%, hsl(19, 24%, 90%) 100%);
  

  /* Font and Typography */
  --body-font: 'Poppins', sans-serif;
  --biggest-font-size: 2rem;
  --h1-font-size: 1.5rem;
  --h2-font-size: 1.25rem;
  --h3-font-size: 1rem;
  --normal-font-size: .938rem;
  --small-font-size: .813rem;
  --smaller-font-size: .75rem;

  /* Font weight */
  --font-medium: 500;
  --font-semi-bold: 600;
  --font-black: 900;

  /* Margin Bottom */
  --mb-0-25: .25rem;
  --mb-0-5: .5rem;
  --mb-0-75: .75rem;
  --mb-1: 1rem;
  --mb-1-5: 1.5rem;
  --mb-2: 2rem;
  --mb-2-5: 2.5rem;

  /* z index */
  --z-tooltip: 10;
  --z-fixed: 100;
  --z-fixed: 100;
}

/* Responsive typography */
@media screen and (min-width: 992px) {
  :root {
    --biggest-font-size: 4rem;
    --h1-font-size: 2.25rem;
    --h2-font-size: 1.5rem;
    --h3-font-size: 1.25rem;
    --normal-font-size: 1rem;
    --small-font-size: .875rem;
    --smaller-font-size: .813rem;
  }
}

/* BASE */
* {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
}

html {
  scroll-behavior: smooth;
}

body {
  margin: var(--header-height) 0 0 0;
  font-family: var(--body-font);
  font-size: var(--normal-font-size);
  background: var(--body-color);
  color: var(--text-color);
}

h1,h2,h3,h4 {
  color: var(--title-color);
  font-weight: var(--font-semi-bold);
}

ul {
  list-style: none;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
  height: auto;
}

/* .btn, */
input {
  border: none;
  outline: none;
}

.btn {
  cursor: pointer;
  font-family: var(--body-font);
  font-size: var(--normal-font-size);
  border: 1px solid var(--first-color-alt);
  padding: 0 0.5rem;
}

/* REUSABLE CSS CLASSES */
.section {
  padding: 4.5rem 0 2rem;
}

.section__title {
  font-size: var(--h2-font-size);
  margin-bottom: var(--mb-2);
  text-align: center;
}

/* LAYOUT */
.container {
  max-width: 968px;
  margin-left: var(--mb-1-5);
  margin-right: var(--mb-1-5);
}

.grid {
  display: grid;
}

.main {
  overflow: hidden;
}

/* NAV */
.nav {
  height: var(--header-height);
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.nav__logo {
  display: flex;
  align-items: center;
  column-gap: .5rem;
  font-weight: var(--font-medium);
}

.nav__logo-img {
  width: 1.25rem;
}

.nav__link,
.nav__logo,
.nav__toggle,
.nav__close {
  color: var(--title-color);
}

.nav__toggle {
  font-size: 1.25rem;
  cursor: pointer;
  user-select: none;
}

@media screen and (max-width: 767px) {
  .nav__menu {
    position: fixed;
    width: 100%;
    background: var(--container-color);
    top: -150%;
    left: 0;
    padding: 3.5rem 0;
    transition: .4s;
    z-index: var(--z-fixed);
    border-radius: 0 0 1.5rem 1.5rem;
    box-shadow: 0 2px 2px rgb(0 0 0 / 40%);
  }
}

.nav__img {
  width: 100px;
  position: absolute;
  top: 0;
  left: 0;
}

.nav__close {
  font-size: 1.6rem;
  position: absolute;
  top: 0.5rem;
  right: 1.5rem;
  cursor: pointer;
  user-select: none;
}

.nav__list {
  display: flex;
  flex-direction: column;
  align-items: center;
  row-gap: 1.5rem;
}

.nav__link {
  text-transform: uppercase;
  font-weight: var(--font-black);
  transition: .4s;
}

.nav__link:hover {
  color: var(--text-color);
}

.show-menu {
  top: 0;
}

/* Change background header */
.scroll-header {
  background: var(--container-color);
}

/* Active link */
.active-link {
  position: relative;
}

.active-link::before {
  content: '';
  position: absolute;
  bottom: .4rem;
  left: -1.5rem;
  width: 8px;
  height: 8px;
  background-color: var(--title-color);
  border-radius: 50%;
}

/* HEADER */
.header {
  width: 100%;
  background: var(--body-color);
  position: fixed;
  top: 0;
  left: 0;
  z-index: var(--z-fixed);
}

/* FOOTER */

/* BUTTONS */
.btn {
  display: inline-block;
  /* background-color: var(--first-color); */
  color: var(--title-color);
  border-radius: .5rem;
  font-weight: var(--font-medium);
  transition: .3s;
  user-select: none;
}

.btn.active,
.btn:hover {
  background-color: var(--first-color-alt);
  color: var(--title-color-light);
}

.btn-lg {
  padding: 1rem 1.75rem;
}
.btn-xl {
  font-size: var(--biggest-font-size);
  padding: 1.5rem 3rem;
}


