/* BREAKPOINTS */
/* For small devices */
@media screen and (max-width: 320px){
  .container{
    margin-left: var(--mb-1);
    margin-right: var(--mb-1);
  }
}

/* For medium devices */
@media screen and (min-width: 576px){}

@media screen and (min-width: 767px){
  body{
    margin: 0;
  }

  .section{
    padding: 7rem 0 2rem;
  }

  .nav{
    height: calc(var(--header-height) + 1.5rem);
  }
  .nav__img,
  .nav__close,
  .nav__toggle{
    display: none;
  }
  .nav__list{
    flex-direction: row;
    column-gap: 3rem;
  }
  .nav__link{
    text-transform: initial;
    font-weight: initial;
  }

  .active-link::before {
    content: '';
    position: absolute;
    bottom: -0.75rem;
    left: 1px;
    width: 100%;
    /* width: 30px; */
    height: 2px;
    background-color: var(--title-color);
    border-radius: 3px;
  }

  section.container {
    min-height: calc(100vh - var(--header-height) - var(--header-margin-top));
    margin-top: var(--header-margin-top);
  }
}

/* For large devices */
@media screen and (min-width: 992px){
  .container{
    margin-left: auto;
    margin-right: auto;
  }
}

@media screen and (min-width: 1200px){}

/* Home */
.home-logo {
  height: 40vmin;
  pointer-events: none;
  margin-bottom: 3rem;
}

section.home {
  min-height: calc(100vh - var(--header-height) - var(--header-margin-top));
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
}

.home .btn {
  width: calc(8rem + 3vmin);
  margin-top: 1rem;
}

/* About */
p.indent {
  text-indent: 2rem;
}
.mb-1 {
  margin-bottom: 1rem;
}
.social-icons {
  text-align: center;
  margin-top: 2rem;
}
.social-icon {
  width: 50px;
  margin-right: 1rem;
}
.footer {
  padding: 1rem;
  text-align: center;
}

/* Language Switcher */
.lang-control {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  margin: 1rem;
  position: relative;
}
.lang-control .btn {
  font-size: .8rem;
  margin-right: 0.5rem;
}
.lang-control .btn.active {
  z-index: var(--z-fixed);
}
.lang-control .btn.collapse {
  display: none;
}
@media screen and (min-width: 767px) {
  .lang-control {
    flex-direction: row-reverse;
  }
}

/* Ads */
.ad {
  position: fixed;
  display: flex;
  justify-content: center;
  align-items: center;
}
.ad-970-90,
.ad-728-90,
.ad-300-50 {
  display: none;
  transition: bottom .4s;
}

.ad-970-90,
.ad-728-90 {
  bottom: -100px;
}
.ad-300-50 {
  bottom: -60px;
}
.ad-970-90.collapse,
.ad-728-90.collapse,
.ad-300-50.collapse {
  bottom: 0;
}
.ad-970-90 { left: calc((100% - 970px) / 2); }
.ad-728-90 { left: calc((100% - 728px) / 2); }
.ad-300-50 { left: calc((100% - 300px) / 2); }
@media screen and (min-width: 971px) { .ad-970-90 { display: block; } }
@media screen and (min-width: 768px) and (max-width: 970px) { .ad-728-90 { display: block; } }
@media screen and (max-width: 767px) { .ad-300-50 { display: block; } }
