.categories {
  display: flex;
  flex-direction: row;
  overflow: hidden;
  margin: .5rem 0 1rem;
}

.categories .btn {
  margin-right: .5rem;
  white-space: nowrap;
}

.level-cards {
  display: grid;
  grid-template-columns: 1fr;
  row-gap: .5rem;
  align-items: center;
  padding: 0.5rem;
}

.level-cards img {
  width: 100%;
  /* margin-bottom: 1rem; */
  border-radius: 10px;
  box-shadow: 0 0 3px 0 rgba(125,125,125,.5);
}

.level-cards img:hover {
  box-shadow: 0 0 5px 0 rgba(243, 58, 58, .7);
}


@media screen and (min-width: 576px){
  .level-cards {
    grid-template-columns: 1fr 1fr;
    column-gap: 1rem;
  }
}
@media screen and (min-width: 767px) {
  .level-cards {
    grid-template-columns: 1fr 1fr 1fr;
    column-gap: 1rem;
  }
}