@media screen and (min-width: 767px) {
  .game.container {
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-column-gap: .5rem;
    width: calc(100% - var(--mb-1-5));
  }  
}

.voca-sheet {
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-row-gap: 2px;
}
.voca-sheet .info {
  display: grid;
  grid-template-columns: 80px 1fr;
  align-items: center;
}
.voca-sheet .info .voca-card {
  width: 80px;
  height: 80px;
  background-color: white;
  display: flex;
  justify-content: center;
  align-items: center;
}
.voca-sheet .info .desc {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  padding-left: .5rem;
}

.splash-screen {
  text-align: center;
  width: inherit;
  margin-bottom: 1rem;
}
.splash-screen img {
  margin-bottom: 0.5rem;
}