.board {
  margin-bottom: 1rem;
}

.board .flip-cards {
  display: flex;
  justify-content: center;
}

.flip-cards {
  display: flex;
  flex-wrap: wrap;
  column-gap: 2px;
  row-gap: 2px;
}

.game-info {
  border: 1px solid rgb(93, 164, 179);
  border-radius: 5px;
  padding: .5rem;
  margin: .5rem;
  text-align: center;
}
.color-red {
  color: red;
}

.level-control-panel {
  display: flex;
  flex-direction: row;
  justify-content: center;
  margin: 0.5rem;
}
.level-control-panel .btn:nth-child(1) {
  margin-right: 1rem;
}